<script>

    import {onMount} from "svelte";
    import html2canvas from "html2canvas";
    import jsPDF from "jspdf"

    const users = [
        {
            login: 'ntw',
            pass: 'a123'
        }
    ]

    let
        leftPech = 2,
        bottomPech = 0,
        leftRosp = 24,
        bottomRosp = 2;

    let saveProject = false, selectFile = false, src;
    let login = '', password = '', auth = false;

    onMount(() => {
        const img = document.getElementById("image");
    })

    const save = () => {
        saveProject = true;
        const canvas = document.getElementById("test");

        html2canvas(document.querySelector(".block")).then(el => {
            el.classList.add('canvas')
            document.querySelector('.result').appendChild(el);
            convert()
        })
    }

    const convert = () => {
        const canvas = document.querySelector('.canvas');

        const quality = 1;
        const image = {
            data: canvas.toDataURL('image/jpeg', quality),
            height: canvas.height,
            width: canvas.width
        }

        createPDF(image);
    }

    const createPDF = (img) => {
        let w = Math.floor(img.width * 0.264583);
        let h = Math.floor(img.height * 0.264583);

        var orientation = w > h ? 'l' : 'p';

        const docPDF = new jsPDF(orientation, 'mm', [w, h]);

        docPDF.addImage(img.data, "JPEG", 0, 0);

        docPDF.output('save', 'test.pdf');
    }

    const fileHandler = (e) => {

        console.log(e)

        if(e.files && e.files[0]) {
            const reader = new FileReader();

            reader.onload = el => {
                const test = document.getElementById("image");
                console.log(test)
                src = el.target.result;
                selectFile = true
            }

            reader.readAsDataURL(e.files[0])
        }
    }

    $: {
        if(password && login) {
            const candidate = users.find(el => {if(el.login === login && el.pass === password) return true; return false});
            if(candidate)
                auth = true
        }
    }
</script>

<div class="container">
    {#if auth}
        {#if !selectFile}
            <input type="file" on:change={e => {console.log('CHANGE'); fileHandler(e.srcElement)}}>
        {:else if !saveProject}
            <div class="block">
                <div class="block__image">
                    <img {src} alt="">
                </div>
                <div class="dop block__rosp" style="bottom: {bottomRosp}%; left: {leftRosp}%;">
                    <img src="./Podpis_2.png" alt="">
                </div>
                <div class="dop block__pech" style="bottom: {bottomPech}px; left: {leftPech}px;">
                    <img src="./pechat.png" alt="">
                </div>
            </div>
            <div class="manager">
                <div class="manager__child">
                    <div class="manager__title">
                        Печать
                    </div>
                    <div class="manager__input">
                        <div class="input">
                            <div class="input__btn" on:click={() => {leftPech -= 1}}>
                                <p>-</p>
                            </div>
                            <p>Сдвиг вправо: <span>{leftPech}</span></p>
                            <div class="input__btn" on:click={() => {leftPech += 1}}>
                                <p>+</p>
                            </div>
                        </div>
                        <div class="input">
                            <div class="input__btn" on:click={() => {bottomPech -= 1}}>
                                <p>-</p>
                            </div>
                            <p>Сдвиг вверх: <span>{bottomPech}</span></p>
                            <div class="input__btn" on:click={() => {bottomPech += 1}}>
                                <p>+</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="manager__child">
                    <div class="manager__title">
                        Роспись
                    </div>
                    <div class="manager__input">
                        <div class="input">
                            <div class="input__btn" on:click={() => {leftRosp -= 1}}>
                                <p>-</p>
                            </div>
                            <p>Сдвиг вправо: <span>{leftRosp}</span></p>
                            <div class="input__btn" on:click={() => {leftRosp += 1}}>
                                <p>+</p>
                            </div>
                        </div>
                        <div class="input">
                            <div class="input__btn" on:click={() => {bottomRosp -= 1}}>
                                <p>-</p>
                            </div>
                            <p>Сдвиг вверх: <span>{bottomRosp}</span></p>
                            <div class="input__btn" on:click={() => {bottomRosp += 1}}>
                                <p>+</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="save"  on:click={() => {save()}}>
                    <p>Сохранить</p>
                </div>
            </div>
        {:else}
            <div class="result"></div>
        {/if}
    {:else}
        <p>Авторизация</p>
        <input type="text" placeholder="Login" bind:value={login}>
        <input type="text" placeholder="Password" bind:value={password}>
    {/if}
</div>


