    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }

    body {
        margin: 0 !important;
    }

    .container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: relative;
        height: 90vh;
        width: 100%;
    }

    .block {
        position: relative;
    }

    .dop {
        position: absolute;
        bottom: 0;
    }

    .block__rosp img {
        width: 65px;
        height: 30px;
    }

    .block__pech img {
        width: 90px;
        height: 90px;
    }

    .manager {
        position: absolute;
        left: 0;
        top: 0;
        width: 30%;
        background: rgba(128, 128, 128, .2);
        padding: 10px;
        margin-top: 20px;
    }

    .manager__title {
        margin-left: 15px;
    }

    .manager__input {
        display: flex;
    }

    .input {
        display: flex;
        align-items: center;
    }

    .input__btn {
        padding: 5px;
        cursor: pointer;
        background: rgba(128, 128, 128, .2);
        transition: .5s;
        transform: scale(.95);
        opacity: .95;
        height: auto;
        margin: 0 15px;
    }

    .input__btn:hover {
        transform: none;
        opacity: 1;
    }

</style>